"use client"

import { useEffect } from "react"
import { useRouter } from "next/navigation"

export default function Product200SeriesPage() {
  const router = useRouter()

  useEffect(() => {
    // 重定向到 Stainless Steel Sheet & Plate 页面的 200 系列部分
    router.replace("/products/stainless-steel-sheet#200-series-grades")
  }, [router])

  return null
}




